import { lazy } from "react";

// use lazy for better code splitting
const Dashboard = lazy(() => import("../pages/Dashboard"));
const Products = lazy(() => import("../pages/Products"));
const ProductDetails = lazy(() => import("../pages/ProductDetails"));
const Banners = lazy(() => import("../pages/Banners"));
const Category = lazy(() => import("../pages/Category"));
const Staff = lazy(() => import("../pages/Staff"));
const Customers = lazy(() => import("../pages/Customers"));
const CustomerOrder = lazy(() => import("../pages/CustomerOrder"));
const Orders = lazy(() => import("../pages/Orders"));
const OrderInvoice = lazy(() => import("../pages/OrderInvoice"));
const Coupons = lazy(() => import("../pages/Coupons"));
const Inventory = lazy(() => import("../pages/Inventory"));
const Enquiry = lazy(() => import("../pages/Enquiry"));
const Notifications = lazy(() => import("../pages/Notifications"));
const Page404 = lazy(() => import("../pages/404"));
const EditProfile = lazy(() => import("../pages/EditProfile"));

/*
//  * ⚠ These are internal routes!
//  * They will be rendered inside the app, using the default `containers/Layout`.
//  * If you want to add a route to, let's say, a landing page, you should add
//  * it to the `App`'s router, exactly like `Login`, `CreateAccount` and other pages
//  * are routed.
//  *
//  * If you're looking for the links rendered in the SidebarContent, go to
//  * `routes/sidebar.js`
 */

// category: { read: true, write: true }
// cuopon: { read: true, write: true }
// customer: { read: true, write: true }
// enquiry: { read: true, write: true }
// inventory: { read: true, write: true }
// notification: { read: true, write: true }
// order: { read: true, write: true }
// products: { read: true, write: true }
// staffs: { read: true, write: true }

const routes = (role) => [
  {
    path: "/dashboard",
    component: Dashboard,
  },
  {
    path: "/products",
    component: role == "Admin" ? Products : null,
  },
  {
    path: "/product/:id",
    component: role == "Admin" ? ProductDetails : null,
  },
  {
    path: "/banners",
    component: role == "Admin" ? Banners : null,
  },
  {
    path: "/category",
    component: role == "Admin" ? Category : null,
  },
  {
    path: "/customers",
    component: role == "Admin" ? Customers : null,
  },
  {
    path: "/customer-order/:id",
    component: role == "Admin" ? CustomerOrder : null,
  },
  {
    path: "/our-staff",
    component: role == "Admin" ? Staff : null,
  },
  {
    path: "/orders",
    component: role == "Admin" ? Orders : null,
  },
  {
    path: "/order/:id",
    component: role == "Admin" ? OrderInvoice : null,
  },
  {
    path: "/coupons",
    component: role == "Admin" ? Coupons : null,
  },
  {
    path: "/inventory",
    component: role == "Admin" ? Inventory : null,
  },
  {
    path: "/enquiries",
    component: role == "Admin" ? Enquiry : null,
  },
  {
    path: "/notifications",
    component: role == "Admin" ? Notifications : null,
  },
  {
    path: "/404",
    component: Page404,
  },
  {
    path: "/edit-profile",
    component: EditProfile,
  },
];

export default routes;
